#certificate-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  overflow-y: auto;
  bottom: 0;
}

#certificate-wrapper img {
  width: auto;
  height: 70vh;
  max-height: calc(90% - 10px);
  max-width: 90%;
  min-width: 0;
  min-height: 0;
  margin: 5px;
  overflow: hidden;
  border-radius: 3px;
}
