nav {
  position: absolute;
  width: calc(100vw - 50px);
  top: 0;
  left: 0;
  height: 100px;
  padding: 0 25px 0 25px;
  background-color: rgb(39, 39, 39);

  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}

nav a {
  color: rgb(191, 149, 240);

  font-size: 30px;
  user-select: none;
  text-decoration: none;
  outline: none;
}

#icons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 250px;
  margin-right: 14px;
}

#icons img {
  cursor: pointer;
  -webkit-user-drag: none;
}
